import React, { useEffect } from 'react';
import EventLayout from 'pages/Event/Event';
// @material-ui/core components
import withStyles from '@material-ui/core/styles/withStyles';
import landingPageStyle from 'assets/jss/material-kit-pro-react/views/landingPageStyle.jsx';

import useGlobal from 'store';
import { CONTACT_US_SEND_MESSAGE_FAIL, CONTACT_US_SEND_MESSAGE_SUCCESS } from 'actions/type';
import { CONTACT_US_SUBJECT, ADMIN_EMAIL } from 'constants/env';

const Event = () => {
  const [globalState, globalActions] = useGlobal();
  const { type, contactUs } = globalState;

  useEffect(() => {
    if (!globalState.type) {
      window.scrollTo(0, 0);
      document.body.scrollTop = 0;
    }
  });

  return (
    <EventLayout />
  );
};

export default withStyles(landingPageStyle)(Event);
